<template>
  <div>
    <!-- Buscador -->
    <section>
      <searcher></searcher>
    </section>

    <!-- Poles -->
    <section class="bg-white text-white">
      <figure>
        <img src="@/assets/img/home-wave-top.png" alt />
      </figure>

      <div class="home-poles-list">
        <h2 class="text-4xl py-6 md:text-6xl lg:text-4xl">- Destinos -</h2>

        <div class="pl-10 py-4 flex flex-col justify-center gap-4 sm:flex-row xs:pl-0">
          <div class="flex flex-row justify-center gap-6 sm:flex-col">
            <Splide class="prueba-slide" :options="opt_slide">
              <SplideSlide v-for="(item, index) in poles" :key="index">
                <div class="flex flex-col items-start gap-6 p-3">
                  <figure
                    class="flex relative w-70x100 sm:m-auto sm:w-100x20 md:w-100x50 lg:w-100x70 xl:w-100x90"
                  >
                    <img
                      :style="{filter: 'drop-shadow(11px 14px 7px #000)', width: '100%'}"
                      :src="item.img"
                      alt="polo1"
                    />
                    <figcaption
                      class="absolute top-0 left-0 w-full leading-10 xs:leading-none sm:rounded sm:text-6xl lg:text-10xl"
                    >{{ item.name }}</figcaption>
                  </figure>
                  <button
                    class="btn-destinos uppercase font-DIN-Bold shadow-h-blue-md bg-orange text-2xl text-white rounded-75 px-8 py-1 md:px-16 md:mx-auto mx-4"
                    @click="goSinglePole(item.keyword, {
                      country: item.country,
                      pole: item.name,                      
                    })"
                  >descubre</button>
                </div>
              </SplideSlide>
            </Splide>            
          </div>
        </div>
      </div>

      <figure>
        <img src="@/assets/img/home-wave-bottom.png" alt />
      </figure>
    </section>

    <!-- offerts -->
    <section class="bg-white py-20 px-3 sm:px-10">
      <h2 class="text-3xl py-6 text-darkBlue md:text-6xl lg:text-4xl">- Ofertas Express -</h2>

      <div class="flex flex-col justify-center flex-wrap gap-6 px-4 sm:flex-row sm:px-10">
        <div class="md:max-w-18rem" v-for="(item, index) in offerts" :key="index">
          <figure class="pb-3">
            <img class="rounded-t-2xl" :src="item.img" :alt="item.pole" />
          </figure>
          <div
            v-match-heights="{el: ['p'], disabled: ['200',200]}"
            class="border-dashed border-2 rounded-b-2xl border-blue px-5 py-3"
          >
            <h3 class="text-4xl text-blue uppercase font-black font-DIN-Medium">{{ item.name }}</h3>
            <span class="font-Clarissa text-blue text-3xl">
              {{
              item.country
              }}
            </span>

            <span class="uppercase block font-DIN-Bold text-blue font-black">desde</span>

            <span
              class="price block font-DIN-Bold text-orange text-5xl leading-3 font-bold pt-4"
            >${{ item.price }}</span>
            <p class="offer-description text-blue bg-blueGray py-3 pt-6 px-4">{{ item.description }}</p>

            <small>{{ item.info }}</small>
          </div>
          <button
            class="btn-booking font-DIN-Bold w-full text-2xl my-5"
            @click="goOffer(item.keyword, {
              country: 'Cuba',
              product: item.name,
              product_name: item.description,
              price: item.price,
            })"
          >{{ item.btn }}</button>
        </div>
      </div>
    </section>

    <!--about us  -->
    <section class="about text-white relative md:block hidden">
      <div
        class="flex flex-col w-full top-6r items-center"
        :style="{backgroundImage:`url(${about_img})`, backgroundRepeat: 'no-repeat', backgroundSize: 'cover', backgroundPosition: 'center center', padding: '40px 0'}"
      >
        <h2
          class="uppercase text-xl py-6 text-white sm:text-2xl md:text-6xl font-DIN-Medium"
        >- bienvenido a iris travel -</h2>
        <p class="text-justify font-DIN text-xl w-70x100" v-html="about_text"></p>
      </div>
    </section>
  </div>
</template>
<script>
import config from "@/config";

import { Splide, SplideSlide } from "@splidejs/vue-splide";
import "@splidejs/splide/dist/css/themes/splide-default.min.css";

import Searcher from "@/components/searcher/Searcher.vue";
import HomeService from "@/services/home.service";
import textServices from "@/services/common/texts.service";
import "@/assets/css/slide-home.css";
import { json } from "body-parser";

export default {
  name: "Home",
  data() {
    return {
      opt_slide: {
        direction: "ltr",
        rewind: true,
        wheel: true,
        pagination: false,
        autoplay: false,
        height: "auto",
        width: "200rem",
        fixedWidth: "30rem",
        fixedHeight: "50rem",
        breakpoints: {
          640: {
            height: "auto",
            width: "270px",
            fixedWidth: "30rem",
            fixedHeight: "10rem"
          }
        }
      },
      poles: [
        {
          img: config.gallery_url + "general/destinos1.png",
          country: "Cuba",
          name: "Cuba",
          keyword: "cuba"
        },
        {
          img: config.gallery_url + "general/destinos2.png",
          country: "Mexico",
          name: "Cancun",
          keyword: "RQ"
        },
        {
          img: config.gallery_url + "general/destinos3.png",
          country: "Republica Dominicana",
          name: "Punta Cana",
          keyword: "RQ"
        }
      ],
      offerts: [],
      about_img: config.gallery_url + "general/quienessomos-imagen.png",
      about_text: ""
    };
  },
  created() {
    const init = [];
    init.push(textServices.getText("about-home"));
    init.push(HomeService.getOffers());
    Promise.all(init).then(res => {
      if (res) {
        this.about_text = res[0]
          ? res[0].description.replace(/\r?\n/g, "<br>")
          : "";
        this.offerts = res[1];
      }
    });
  },
  components: {
    Searcher,
    Splide,
    SplideSlide
  },
  methods: {
    goSinglePole(keyword, params) {
      if (keyword == "RQ") {
        this.sendRequest(params);
      } else {
        this.$router.push({
          name: "SinglePole",
          params: { keyword }
        });
      }
    },
    goOffer(keyword, params) {
      if (keyword == "RQ") { //los casos que tengan RQ es directo a la pagina de request (envio de solicitud)
        console.log(params)
        this.sendRequest(params);
      } else {
        this.$router.push({
          name: "SinglePole",
          params: { keyword }
        });
      }
    },
    sendRequest(params) {
      localStorage.removeItem('request_info')
      const request = {
        country: (params.hasOwnProperty('country')) ? params.country : 'Cuba',
        pole: (params.hasOwnProperty('pole')) ? params.pole : '',
        date: (params.hasOwnProperty('date')) ? params.date : '1900-01-01/1900-01-05',
        product: (params.hasOwnProperty('product')) ? params.product : 'hotel',
        product_name: (params.hasOwnProperty('product_name')) ? params.product_name : '',
        price: (params.hasOwnProperty('product_name')) ? params.price : 0,
        num_person: 0,
        num_rooms: 0,
        is_home_request: true        
      }            
      localStorage.setItem('request_info', JSON.stringify(request))
      this.$router.push({ name: "SendRequest" });
    }
  }
};
</script>
<style scoped>
.home-poles-list {
  background: linear-gradient(
    360deg,
    rgba(32, 38, 88, 1) 0%,
    rgba(9, 9, 121, 1) 36%,
    rgba(36, 54, 131, 1) 100%
  );
}
.home-poles-list figure figcaption {
  font-size: 3rem;
  font-family: Clarissa;
  top: calc(50% - 100px) !important;
  /* background: rgba(0,0 ,0 ,0.6 ); */
}
.price {
  text-shadow: -2px 2px #232e6e;
}
.about .container {
  top: 80px;
  left: 10em;
}

@media screen and (max-width: 640px) {
  .home-poles-list figure figcaption {
    font-size: 4rem;
    top: calc(70% - 100px) !important;
  }
  .about .container {
    top: 10px;
    left: 0em;
  }
}
.offer-description {
  max-height: 120px !important;
}
</style>
